import React from "react";
import {NavLink} from "react-router-dom";
import "./style.css";
import {Col, Row} from "react-bootstrap";
import github from "../assets/GitHub_Logo.png"
import linkedin from "../assets/Linked-In.png"

export default class Footer extends React.Component{

 
//////////////////////////////////////////////////
// RENDERS PAGE FOOTER
//////////////////////////////////////////////////

render(){
    return(

  <footer  style={{
    position: "fixed",
    bottom: "0",
    width: "100%"}} >
  <Row>
  <div className="col-5"></div>
        <div className="footercenter col-1" >
      <a className="justify-content-center nav-link linkedinlink" href="https://www.linkedin.com/in/robert-sunde-6441b31ba/" target="_blank"><img
          className="linkedin" src={linkedin} alt="linkedin"></img></a>
          </div>

          <div className="footercenter col-1" >
      <a className="justify-content-center nav-link githublink" href="https://github.com/robertsunde" target="_blank"><img className="github"
          src={github} alt="github"></img></a>
          </div>
          <div className="col-5" ></div>
          </Row>
  </footer> 





)}}